<script>
export default {
  useName: 'BasisInfo',
  props: {
    userName: String
  },
  methods: {
    describeState(percentage) {
      if (percentage < 60)
        return "了解";
      if (percentage < 70)
        return "使用";
      if (percentage < 80)
        return "理解";
      if (percentage >= 80)
        return "熟练"
      return ""
    },
    describeWithColor(percentage) {
      let colorStr = (185 - percentage * 2).toString(16);
      if (colorStr.length === 1) {
        colorStr = `0${colorStr}`
      }
      return `#${colorStr}${colorStr}${colorStr}`;
    }
  },
  data() {
